import L from 'leaflet'
import "leaflet/dist/leaflet.css"
import * as Provider from "leaflet.chinatmsproviders"; //
//  leaflet.markercluster
import "leaflet.markercluster/dist/MarkerCluster.css"
import "leaflet.markercluster/dist/MarkerCluster.Default.css"
import "leaflet.markercluster";

//  maker 
import icon from "leaflet/dist/images/marker-icon.png";
import iconShadow from "leaflet/dist/images/marker-shadow.png";
let DefaultIcon = L.icon({
  iconUrl: icon,
  shadowUrl: iconShadow,
});
L.Marker.prototype.options.icon = DefaultIcon;
export const mapUtils = {
  addLayer:(map,map_config,type)=>{
    L.tileLayer.chinaProvider(type, {
      maxZoom: map_config.maxZoom,
      minZoom: map_config.minZoom,
      key: map_config.key
    }).addTo(map);
  },
  createMakerCluster: () => {
    return L.markerClusterGroup();
  },
  createMakerByLatlng:(lat,lng)=>{
    return L.marker([lat,lng],{
      icon:DefaultIcon
    })
  },
  removeLayer:(layers,map)=>{
    if (layers) {
      map.removeLayer(layers);
    }
  },
  clearLayers:(layers,map)=>{
    if (layers) {
      map.clearLayers(layers);
    }
  },

  /*
   * @param x {Object} 1
   * @param y {Object} 2
   * @return  {Boolean} true false 
   */
  ifExsist :  (arr, obj)=> {
    if(JSON.stringify(arr).indexOf(JSON.stringify(obj)) === -1){
      return false;  //
    }else{
     return true //
    }
  },
  //data    
  TofindIndex:(data,flag)=>{
    console.log(data);
    data.findIndex((value, index, arr)=> {
      return value['mMessageType'].indexOf(flag)!=-1
    })
  },
  //
  ToFindAllIndex:(data,flag,type)=>{
    let indexs = [];
    data.map((value, index, arr)=> {
      if(value[type].indexOf(flag)!=-1){
        indexs.push(index);
      }
    })
    return indexs
  },




  scrollUp:(loading,tableData,indexUp,data,highLight) =>{
    loading = true;
    setTimeout(() => {
      const length = tableData.length;
      loading = false;
      indexUp--;
      if (indexUp > 0) {
        data = tableData.slice(indexUp * 10, highLight[0]).concat(data);
      } else {
        data = data.concat(tableData.slice(0, highLight[0]));
      }
    }, 500);
  },

//leaflet
  calcCoorArr:(point_start,point_end,num)=>{// "114.30911,30.600052",num

    //,

    var p_start={x:parseFloat(point_start.split(",")[0]),y:parseFloat(point_start.split(",")[1])};

    var p_end={x:parseFloat(point_end.split(",")[0]),y:parseFloat(point_end.split(",")[1])};

    //180

    // p1(x1,y1)p2(x2,y2)AB,x3y3cot25=2.14

    //x3 = (x1*cotB+x2*cotA+y2-y1)/(cotA+cotB)

    //y3 = (y1*cotB+y2*cotA+x1-x2)/(cotA+cotB)

    let x3=(p_start.x*2.14+p_end.x*2.14-p_start.y+p_end.y)/(2*2.14)

    let y3=(p_start.y*2.14+p_end.y*2.14-p_end.x+p_start.x)/(2*2.14)

    var p_crt1={x:x3,y:y3};

    var p_crt2={x:x3,y:y3};

    //

    /**

      * 

      *         | 1 0 0 0| |P0|

      * [1 t t*t t*t*t] |-3 3 0 0| |P1|

      *         |3 -6 3 0| |P2|

      *         |-1 3 -3 1| |p3|

      *

      * **/

    let paths=[];

    for(let i=0;i<num+1;i++){

      let t=i/num;

      var _matrix1=[1,t,t*t,t*t*t];

      var _matrix2=[

        [1,0,0,0]

        ,[-3,3,0,0]

        ,[3,-6,3,0]

        ,[-1,3,-3,1]

      ];

      var _matrix3=[

        [p_start.x,p_start.y]

        ,[p_crt1.x,p_crt1.y]

        ,[p_crt2.x,p_crt2.y]

        ,[p_end.x,p_end.y]

      ];

      var _matrix_tmp=[

        _matrix1[0]*_matrix2[0][0]+_matrix1[1]*_matrix2[1][0]+_matrix1[2]*_matrix2[2][0]+_matrix1[3]*_matrix2[3][0]

        ,_matrix1[0]*_matrix2[0][1]+_matrix1[1]*_matrix2[1][1]+_matrix1[2]*_matrix2[2][1]+_matrix1[3]*_matrix2[3][1]

        ,_matrix1[0]*_matrix2[0][2]+_matrix1[1]*_matrix2[1][2]+_matrix1[2]*_matrix2[2][2]+_matrix1[3]*_matrix2[3][2]

        ,_matrix1[0]*_matrix2[0][3]+_matrix1[1]*_matrix2[1][3]+_matrix1[2]*_matrix2[2][3]+_matrix1[3]*_matrix2[3][3]

      ];

      var _matrix_final=[

        _matrix_tmp[0]*_matrix3[0][0]+_matrix_tmp[1]*_matrix3[1][0]+_matrix_tmp[2]*_matrix3[2][0]+_matrix_tmp[3]*_matrix3[3][0]

        ,_matrix_tmp[0]*_matrix3[0][1]+_matrix_tmp[1]*_matrix3[1][1]+_matrix_tmp[2]*_matrix3[2][1]+_matrix_tmp[3]*_matrix3[3][1]

      ];

      //leaflet

      // var _res_point={

      // x:_matrix_final[0]

      // ,y:_matrix_final[1]

      // };

      var _res_point=[_matrix_final[1],_matrix_final[0]];

      paths.push(_res_point);

    }

    return paths;

  }

}




