import axios from 'axios'
// import { throwErr } from '@/utils/throwErr' //utils http
import store from '@/store'   //vuex
import { message } from 'ant-design-vue' //element Toast
import router from '@/router'

/**
 * 
 * 
 */
const tip = msg => {
  message.warning(msg)
}
const tipError = msg => {
  message.error(msg)
}
const tipSuccess = msg => {
  message.success(msg)
}

/**
 * 
 * 
 */
const toLogin = () => {
  router.replace({
    path: '/',
    query: {
      redirect: router.currentRoute.fullPath
    }
  });
}

/**
 * 
 * @param {Number} status 
 */
const errorHandle = (status, other) => {
  // 
  switch (status) {
    // 401: 
    case 401:
      toLogin()
      break
    // 403 token
    // token
    case 403:
      tip('')
      // localStorage.removeItem('token')
      // store.commit('loginSuccess', null);
      setTimeout(() => {
        toLogin()
      }, 1000)
      break
    // 404
    case 404:
      tip('')
      break
    default:
  }
}

const toastHandle = (data) => {
  const { errorCode, errorMsg } = data;
  if (errorCode === '0002') {
    tipError('')
  } else if (errorCode === '0001') {
    tip(errorMsg)
    if (errorMsg === '!') {
      toLogin()
    }
  } else if (errorCode === '0000') {
    if (errorMsg !== '' && errorMsg !== '!' && errorMsg !== '!') {
      tipSuccess(errorMsg)
    }
  }
}
// 
let token = '';
axios.defaults.withCredentials = false;
axios.defaults.headers.common['token'] = token;
axios.interceptors.request.use(config => {
  // config    Token  axios  
  // 
  // config.timeout = 30 * 1000 // 
  let userToken = localStorage.getItem("token");
  if (userToken) {
    token = userToken;
  }
  config.headers.common['token'] = token;
  //console.dir(config);
  return config
}, error => {
  return Promise.reject(error)
})
