export default function request(method, url, data,responseType) {  // request API 
  method = method.toLocaleLowerCase()   //RESTful API  post get delete
  if (method === 'post') {
    return axios.post(url, data,responseType)    //axiospost json
  } else if (method === 'get') {
    return axios.get(url, {
      params: data
    })
  }
}



